.main>h1,h2,h3,h4,p{color:#3D2C8D}
.total{
    padding: 5vh 0px;
    display: flex;
    height: 75vh;
    overflow: hidden;
    background-image: url("./wave-haikei.png");
    background-size: contain;
    background-color: rgb(0, 0, 0);
    
}
.left{
    flex: 1;
}
.right{
    flex: 1;
    margin-right:  5vw;
    height: 100%;
    width: 100%;
    
}
.intro{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: left;
}
.Noo{font-size: 40px;}
.name{
    font-size: 10vw;
    /* color:#C996CC; */
    color: white;
    text-transform: uppercase;
    animation: tracking-in-contract-bck 2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    
}
.item{
    font-size:  50px ;
    color: cadetblue;
    display: flex;
    align-items: center; 
}
.i-title{
    height: 60px;
    width: 30vw;
    overflow: hidden;
}
.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}
@keyframes move {
    25%{
        transform: translateY(-60px);
    }
    50%{
        transform: translateY(-120px);
    }
    100%{
        transform: translateY(-150px);
    }
}
.photo{
    object-fit: fill;
    width: 100%;
    height: 90%;
    display: block;
}
.clip{
    width: 100%;
    height: 80%;
    
}

@media (max-width:425px) {
    .main{z-index: 0;background-color: none!important;display: block;}
    .total{height:auto;width: 100vw;display: block;position: relative;background-image: none;}
    .Noo{margin: 0px;font-size: 40px;display: flex;align-items: center;justify-content: center;}
    .name{display: flex;align-items: center;justify-content: center;font-size: 100px;}
    .i-title{width: 80vw;height:25px}
    .left{height: 80vh;}
    .item{font-size: 20px;display: flex;align-items: center;justify-content: center;font-weight: bold;}
    .i-title-wrapper{height: 100%;animation: move-mobile 10s ease-in-out infinite alternate;}

@keyframes move-mobile {
    25%{
        transform: translateY(-25px);
    }
    50%{
        transform: translateY(-50px);
    }
    100%{
        transform: translateY(-80px);
    }
}
}
@keyframes tracking-in-contract-bck {
  0% {
    letter-spacing: 1em;
    -webkit-transform: translateZ(400px);
            transform: translateZ(400px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
}
.social{
    display: flex;
    padding:8vh 2vw;
    justify-content:space-between;
    font-size: 2em;
    max-width: 15vw;    
}
.social>*{
    color: white;
    display: flex;
    align-items: center;
    padding: 10px;
}
.social>*:hover{
    transform: translateY(-2px);
    border-radius: 25px;
    background-color: #B42B51;
}
