.mainn{
    padding: 0vh 10vw;
    height: auto;
    background-color: #000000; 
}
.mainn  h1,h2,h3,h4{
    color: #ff8013;
}
p{font-size: 1.4em;color: aliceblue;}
.edu{
    display: flex;
    text-decoration: underline 2px solid white!important;
    align-items: center;
    justify-content: center;
    padding:5vh 0px;
} 
.lefty{
    padding: 5vh 0px;
    left: 0px!important;
}
.division{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2vw;
    padding-top: 5vh;

}
.Engineering{
    padding: 10px;
}
.Secondary{
    padding: 10px;
}
.certificat>h4::before{
    content: "*";
    margin: 10px;
    padding-top: 4px;
}
.skille>*{
    display: flex;
    align-items: center;
    justify-content:baseline;
    flex-direction: row;
    padding: 5vh 0px;
    flex-wrap: wrap;
}
.i{margin-right: 1vw;font-size: 0.7em;}
.skille>p>li{margin:2vh 5vw;color: rgb(145, 228, 67);list-style: none;}
li{
   list-style-type:square;
}
.half{
    display: flex;
}
.first{
    flex: 1;
}
.second{
    flex: 1;
}

@media (max-width:425px) {
    .division{
         display: block; 
        max-width: 100vw;
        overflow-x: hidden;
        position: relative;
    }
    .mainn{display:block;position: relative;} 
    .half{display: block;}
    .skille>*{justify-content: center};

}
