iframe{
    width: 100vw;
    height: 100vh;
}
.download{
    height: 10vh;  
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.download>h1{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: cadetblue;
    width: 30vw;
    border-radius: 2%;
    color: blanchedalmond;

}
.ic{
    width: 5vw;
    padding-right: 0.5vw;
}
@media (max-width:1000px){
    .download>h1{font-size: 0.7em; width: 70vw;height: 75%;}
   

}