.navee{
    min-height: 15vh;
    background-color:rgb(0, 0, 0);
    position: sticky;
    top: 0px;
    z-index: 10;
    display: flex;
}
.logoo{
    flex: 0.7;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    font-size: 3em;
    color: #DDDDDD;
    
}
.contaact{flex: 0.5;display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 1.6em;
    float: left;
}
.contaact>*{padding-right: 3vw;color: burlywood;}
.contaact>*:hover{color: rebeccapurple;transform: scale(1.2);}
.main-menu{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 1.4em;
    font-weight: 300px;    
}
.main-menu>*{font-family: 'Roboto', sans-serif;color: burlywood;}
.main-menu>*:hover{color: rebeccapurple;transform: scale(1.2);}
a{text-decoration: none;}
h4{
    color: white;
    font-size: 1.2em;
}
.active {
    font-weight: 900;
    color: #deb887;
    padding: 10px;
    background-color: chocolate;
    border-radius: 17%;
}
.ham{
    display: none;
}

@media (max-width:427px){
    .navee{height: fit-content;}
    .full{
        display: flex;  
        flex-direction: column;
        
    }
    .logoo{
        display: flex;
        align-items: center;
        width: 100%;
        font-size: 1.3em;
    }
    .cta{display: flex;font-size: 0.7em;padding-right: 10vw;}
    .cta>p{display: none;}
    .ham{
        display: flex;
        position: absolute;
        top: 5vh;
        right: 10vw;
        width: 15vw;
        z-index: 11;
        justify-content: center;
        height: 5vh;
        color: grey;
        display: block;
      }
      .mobile{
        background-color: rgb(0, 0, 0);
        display: flex;
        flex-direction: column;
        position: absolute;
        width: 100vw;
        top: 15vh;
        color: white;
        height: 35vh;
        
        justify-content: space-around;
        align-items: center;
      }
      .main-menu{display: none;}

}

.Main-logo{
    height: 6vh;
    color: white;
    font-family: 'Praise', cursive;
    font-weight: bold;
}

.Main-logo:hover{
    color: #c996cc;
    cursor: pointer;
}