
.contact-email{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: underline;
    text-decoration-line: 5px solid;
    text-decoration-color: aqua;
}
form{
    display: flex;
    flex-direction: column;
    width: 100vw;
    align-items: center;
}
form>*{
    width: 50vw;
    padding: 1vh 0vw;
    margin: 0vh 1vw 2vh 1vw;
}
form>label{color: white;text-decoration: underline;font-weight: bold;font-size: 1.5em;}

input,textarea{
    font-size: 1.2em;
    font-weight: bolder;
}

@media (max-width:425px){
    form>*{width: 80vw;}
}
.contact-email{
    color:#ff8013;
}